.image {
  max-width: 100%;
  margin-bottom: 25px;
}

.wrapper {
  min-height: 275px;
}

.comments-wrapper__comment,
.input-form {
  padding: 0 22px;
  width: 100%;
}

.comments-wrapper {
  max-height: 30%;
  flex: 0 1 auto;
  overflow: scroll;
  margin-bottom: 20px;
}

.comments-wrapper__comment__date {
  display: block;
  margin-bottom: 5px;
  color: #999;
  font-size: 13px;
}

.comments-wrapper__comment__text {
  margin-bottom: 20px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

.container {
  display: flex;
  height: 100%;
  flex-flow: column;
  background-color: white;
}

.input-form__name,
.input-form__comment {
  height: 30px;
  width: 100%;
  margin-bottom: 20px;
  border: 1px solid #cccccc;
  border-radius: 3px;
  padding: 8px;
}

.input-form__name::-webkit-input-placeholder, .input-form__comment::-webkit-input-placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__name::-moz-placeholder, .input-form__comment::-moz-placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__name:-ms-input-placeholder, .input-form__comment:-ms-input-placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__name::-ms-input-placeholder, .input-form__comment::-ms-input-placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__name::placeholder,
.input-form__comment::placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__button {
  width: 100%;
  padding: 8px;
  text-align: center;
  color: white;
  font-size: 13px;
  line-height: 15px;
  border: none;
  background-color: #4997D0;
  border-radius: 3px;
}

@media screen and (min-width: 500px) {
  .image {
    max-height: 320px;
  }

  .input-form {
    width: 480px;
    height: 100%;

  }
  .container {
    position: absolute;
    width: 80%;
    max-width: 900px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    flex-flow: row wrap;
    height: auto;
    padding: 30px;
  }
  .comments-wrapper {
    overflow: auto;
    width: 100%;
    max-height: 350px;
  }
  .comments-wrapper__comment__date:first-of-type {
    margin-top: 5px;
  }
  .input-form {
    padding: 0;
  }
}

@media screen and (min-width: 768px) {
  .comments-wrapper {
    width: 35%;
  }
}

