.modal {
  position: fixed;
  overflow-y: hidden;
  width: 100vw;
  height: 100vh;
  background-color: white;
  z-index: 100;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.modal button {
  border: none;
  background: none;
  position: absolute;
  top: 21px;
  right: 21px;
}
.close-button {
  cursor: pointer;
}

@media screen and (min-width: 500px) {
  .modal {
    position: fixed;
    top: 0;
