.gallery {
  flex: 1 1;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-content: flex-start;
  width: calc(100% - 40px);
  margin: 0 auto;
}

.img {
  width: 100%;
  height: auto;
  margin-bottom: 30px;
  background: black;
  object-fit: contain;
  cursor: pointer;

}

.gallery__loading {
  margin: 0 auto;
  font-size: 30px;
}

@media screen and (min-width: 500px) {
  .gallery {
    flex-flow: row wrap;
    width: 100%;
  }

  .img {
    width: auto;
  }
}

.footer {
  height: 56px;
  width: calc(100vw - 40px);
  position: relative;
  left: calc(-50vw + 50% + 20px);
  border-top: 1px solid #ccc;
  text-align: center;
}

.footer-copyright {
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 56px;
  color: #ccc;
}

.App {
  min-height: 100vh;
  max-width: 960px;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  margin: 0 auto;

}

.App-header {
  width: 100%;
}

.App-header__title {
  font-family: Roboto Condensed, serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 42px;
  margin-top: 15px;
  margin-bottom: 30px;
  text-align: center;
  text-transform: uppercase;

  color: #000000;
}

